import React, { useRef, useState, useEffect } from "react";
import { Col, Row, Container, Button, Form } from "react-bootstrap";
import { navigate } from "gatsby";

import "../components/pages-css/sCita.css";
import Layout from "../components/layout.js";
import Seo from "../components/seo";
import VideoSeccion from "../components/videoSeccion.js";
import ModalEmergente from "../components/ModalEmergente.js";
import Keyboards from "../components/Keyboards.js";
import Timer from "../components/timer.js";

import diagnostico from "../images/iconos/btn_diagnostico.svg";

export default function RealizarDiagnostico(){

    const [show, setShow] = useState(false);
    const [modal, setModal] = useState(false);
    const [errorInput, setErrorInput] = useState(false);
    const [error, setError] = useState(0);
    const [click, setClick] = useState(false);

    const [optionsKeyboard, setOptionsKeyboard] = useState({
        inpName: "",
        layoName: "default",
        name: "",
        value: {
            Diagnostico: undefined
        }
    })

    const refForm = useRef();

    useEffect(() => {
        if (typeof window !== 'undefined') {
            //console.log('we are running on the client');
            setOptionsKeyboard({
              ...optionsKeyboard,
              value: {
                Diagnostico: localStorage.getItem("Diagnostico") || "",
              }
            })
        } else {
            //console.log('we are running on the server');
        }
    }, [])

    function continuar(){
       if(optionsKeyboard.value.Diagnostico === "" || optionsKeyboard.value.Diagnostico === undefined){
            setError(0);
            setModal(true);
            setErrorInput(true);
       } else {
            //console.log("Me han pulsado jiji: ", optionsKeyboard.value.Diagnostico);
            localStorage.setItem('Diagnostico', optionsKeyboard.value.Diagnostico);
            navigate("/imprimir-diagnostico")
       }
    }

    return(
        <Layout backUrl={"/servicio-a-realizar"}>
            <Seo title="Auto servicio" />
            <Container className="container-sin-cita" onClick={() => {setClick(true)}}>
            <Timer click={click} setClick={setClick} setError={setError} setModal={setModal}/>
                <Row className="row-cita-arriba">
                    <div className="container-cita">
                        <Row>
                            <Col className="titulo-cita"><h1 className="titulo-h1">Servicio de Diagnóstico</h1></Col>
                        </Row>

                        <Container>
                            <Row className="titulo-cita">
                                <Col lg={4} >
                                    <Row style={{backgroundColor: '#deecff', borderRadius: 50, margin: 10}}>
                                        <img src={diagnostico} style={{height: '150px', marginBottom: 0}}/> 
                                        <div style={{display: 'flex', justifyContent: 'center'}}><h5 className="titulo-h1">Diagnóstico</h5></div>
                                    </Row>
                                </Col>
                                <Col lg={8} >
                                    <Form.Control
                                        as="textarea" 
                                        placeholder="Cuéntame... ¿Qué le pasa a tu carro?" 
                                        rows={8} 
                                        id={errorInput === true ? "inputinputDiagnosticoError" : "inputDiagnostico"}
                                        onClick={() => setShow(true)}
                                        onFocus={() => setOptionsKeyboard({...optionsKeyboard, inpName: "Diagnostico"})}
                                        value={optionsKeyboard.value.Diagnostico || ""}
                                    />
                                    <p className="texto-gris">* Costo del diagnóstico $2,970 mxn el cual se cancela al realizar la reparación.</p>
                                </Col>
                                <Button ref={refForm} onClick={() => continuar()} style={{display: 'none'}}></Button>

                                <center className="pt-5">
                                    <Keyboards options={optionsKeyboard} setOptionsKeyboard={setOptionsKeyboard} optionsKeyboard={optionsKeyboard}/>
                                </center>
                            </Row>
                        </Container>
                    </div>
                </Row>
                <Row className="row-cita-abajo">
                    <VideoSeccion refForm={refForm} none="tiene"/>
                </Row>
            </Container>
            <ModalEmergente modal={modal} error={error} setModal={setModal} setClick={setClick}/>
        </Layout>
    )

}